<template>
  <el-dialog v-model="dialogVisible" size="small" :show-close="false" :top="top" @close="onClose">
    <div slot="title">
      <div class="el-dialog__headerbtn">
        <router-link :to="{name: 'UserBargains'}"><i class="el-dialog__close el-icon el-icon-close"></i></router-link>
      </div>
    </div>
    <slot>
      <div class="container add-bargain">
        <h3>Add Bargain</h3>
        <div class="modal-body">
          <div class="row">
            <div class="col-lg-12 reset-col">
              <p class="reset-col bb-label-12"><b>Title:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('title')" class="help is-danger" >{{ errors.first('title') }}</span></p>
              <div class="input-group">
                <el-input v-model="title" v-validate:title="'required'" :class="{'is-danger': errors.has('title') }" name="title" type="text" placeholder="Bargain Title"></el-input>
                <i v-show="errors.has('title')" class="fa fa-warning" style="position: absolute; right: 5px; top: 10px;"></i>
              </div>
            </div>
          </div>
          <div class="row row-pad-top">
            <div class="col-lg-8 col-md-8 reset-col">
              <div class="row">
                <div class="col-lg-6 col-md-6 reset-col row-pad-right">
                  <p class="reset-col bb-label-12"><b>Type:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('selectedType')" class="help is-danger" >{{ errors.first('selectedType') }}</span></p>
                  <el-select v-model="selectedType" data-vv-as="type" v-validate:selectedType="'required'" name="selectedType" placeholder="Select a Bargain's Type">
                      <el-option
                        v-for="item in typeOptions"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
                </div>
                <div class="col-lg-6 col-md-6 reset-col row-pad-left">
                  <p class="reset-col bb-label-12"><b>Percent off:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('percent')" class="help is-danger" >{{ errors.first('percent') }}</span></p>
                  <div class="input-group">
                    <el-input v-model="percent" :disabled="selectedType !== 'percentOff'" data-vv-as="percent" placeholder="Percent Off" name="percent"  :controls="false" :min="0" :max="100">
                      <template slot="prepend">%</template>
                    </el-input>
                  </div>
                </div>
              </div>
              <div class="row row-pad-top">
                <div class="col-lg-6 col-md-6 reset-col row-pad-right">
                  <p class="reset-col bb-label-12"><b>List Price:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('listPrice')" class="help is-danger" >{{ errors.first('listPrice') }}</span></p>
                  <div class="input-group">
                    <el-input v-model="listPrice" data-vv-as="list price" :disabled="selectedType !== 'sale'" placeholder="List Price" name="listPrice">
                      <template slot="prepend">$</template>
                    </el-input>
                  </div>
                </div>
                <div class="col-lg-6 col-md-6 reset-col row-pad-left">
                  <p class="reset-col bb-label-12"><b>Sale Price:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('salePrice')" class="help is-danger" >{{ errors.first('salePrice') }}</span></p>
                  <div class="input-group">
                    <el-input v-model="salePrice" data-vv-as="sale price" :disabled="selectedType !== 'sale'"  placeholder="Sale Price"  name="salePrice">
                      <template slot="prepend">$</template>
                    </el-input>
                  </div>
                </div>
              </div>

              <div class="row row-pad-top">
                <div class="col-lg-6 col-md-6 reset-col row-pad-right">
                  <p class="reset-col bb-label-12"><b>Store:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('selectedStore')" class="help is-danger" >{{ errors.first('selectedStore') }}</span></p>
                  <el-select v-model="selectedStore" data-vv-as="store" @change="storeChange" filterable v-validate:selectedStore="'required'" name="selectedStore" placeholder="Select a store">
                    <el-option
                      v-for="item in storeOptions"
                      :label="item.name"
                      :value="item._id">
                    </el-option>
                  </el-select>
                </div>
                <div class="col-lg-6 col-md-6 reset-col row-pad-left">
                  <p class="reset-col bb-label-12"><b>Brand:</b></p>
                  <el-select v-model="selectedBrand" name="selectedBrand" filterable placeholder="Select a brand">
                    <el-option
                      v-for="item in brandOptions"
                      :label="item.name"
                      :value="item._id">
                    </el-option>
                  </el-select>
                </div>
              </div>

              <div class="row row-pad-top">
                <div class="col-lg-6 col-md-6 reset-col row-pad-right">
                  <p class="reset-col bb-label-12"><b>Valid on:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('validOn')" class="help is-danger" >{{ errors.first('validOn') }}</span></p>
                  <el-date-picker
                    v-model="validOn"
                    type="datetime"
                    :class="{'help is-danger': errors.has('validOn')}"
                    v-validate:validOn="'required'"
                    name="validOn"
                    placeholder="Pick a valid on day"
                    style="width: 100%;">
                  </el-date-picker>
                </div>

                <div class="col-lg-6 col-md-6 reset-col row-pad-left">
                  <p class="reset-col bb-label-12"><b>Valid until:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('validUntil')" class="help is-danger" >{{ errors.first('validUntil') }}</span></p>
                  <el-date-picker
                    v-model="validUntil"
                    type="datetime"
                    :class="{'help is-danger': errors.has('validUntil')}"
                    v-validate:validUntil="'required'"
                    name="validUntil"
                    placeholder="Pick a valid until day"
                    style="width: 100%;">
                  </el-date-picker>
                </div>
              </div>

              <div class="row row-pad-top">
                <div class="col-lg-12 col-md-12 reset-col">
                  <p class="reset-col bb-label-12"><b>Categories:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('selectedCategory')" class="help is-danger" >{{ errors.first('selectedCategory') }}</span></p>
                  <el-select v-model="selectedCategory" data-vv-as="categories" v-validate:selectedCategory="'required'" name="selectedCategory" multiple filterable placeholder="Select categories">
                    <el-option
                      v-for="item in categoryOptions"
                      :label="item.name"
                      :value="item._id">
                    </el-option>
                  </el-select>
                </div>
              </div>

              <div class="row row-pad-top">
                <div class="col-lg-6 col-md-6 reset-col row-pad-right">
                  <p class="reset-col bb-label-12"><b>Main url:<i style="color:#d32f2f;">* </i></b><span v-show="errors.has('mainUrl')" class="help is-danger" >{{ errors.first('mainUrl') }}</span></p>
                  <el-input placeholder="Enter Main Url https://www."
                            v-model="mainUrl"
                            name="mainUrl"
                            data-vv-as="main url"
                            v-validate:mainUrl="'required'">
                    <template slot="append"><a target="_blank" :href="isHttp ? mainUrl : 'http://' + mainUrl"><el-button slot="append">View Deal</el-button></a></template>
                  </el-input>
                </div>
                <div class="col-lg-6 col-md-6 reset-col row-pad-left">
                  <p class="reset-col bb-label-12"><b>Product id:</b></p>
                  <el-input placeholder="Product ID"
                            v-model="productId"
                            name="productId">
                  </el-input>
                </div>
              </div>
            </div>
            <div class="col-lg-4 col-md-4 reset-col row-pad-left text-center">
              <el-upload
                class="upload-demo"
                drag
                action=""
                :file-list="fileList"
                :show-file-list="true"
                :on-remove="onFileRemove"
                :before-upload="onFileSelected"
                accept="image/jpeg"
                mutiple>
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">Drop file here or <em>click to upload</em></div>
                <div class="el-upload__tip" slot="tip">jpg/png files with a size less than 500kb</div>
              </el-upload>
            </div>


            <div class="row clearfix" style="margin-top:20px;">
              <div class="col-lg-12 reset-col">
                <span class="control-label">Bargain's summary: </span>
                <div class="clearfix" style="margin-top:10px;">
                  <quill-editor ref="myTextEditor"
                                v-model="summary"
                                class="clearfix"
                                :config="editorOption" style="min-height:75px; clear:both; margin-bottom:10px;">
                  </quill-editor>
                </div>
              </div>
            </div>


            <div class="row clearfix">
              <div class="col-lg-12 reset-col">
                <span class="control-label">Bargain's details: </span>
                <div class="clearfix" style="margin-top:10px;">
                  <quill-editor ref="myTextEditor"
                                v-model="content"
                                class="clearfix"
                                :config="editorOption" style="min-height:200px; clear:both;">
                  </quill-editor>
                </div>
              </div>
            </div>



            <div class="a-row row-pad-top">
              <div class="col-lg-12 reset-col">
                <el-select v-model="selectedTag"  name="selectedTag" multiple filterable placeholder="Select Tags">
                  <el-option
                    v-for="item in tagOptions"
                    :label="item.name"
                    :value="item._id">
                  </el-option>
                </el-select>
              </div>
            </div>

          </div>
        </div>
        <div class="footer pull-right" style="margin-top:20px; margin-bottom:10px;">
          <el-button type="secondary" @click="onClose">Cancel</el-button>
          <el-button type="primary " @click="validateBeforeSubmit">Add Bargain</el-button>
        </div>
      </div>
    </slot>
  </el-dialog>
</template>
<style>
  @media (min-width: 576px) {
    div.my-bargain div.el-dialog__body .container {
      width:100%;
    }
  }

  .el-select {
    display: block;
  }


  .row-pad-top {
    padding-top: 10px;
  }

  .el-upload-list__item.is-ready {
    display: none;
  }

  .bb-label-12 {
    font-size:13px;
  }

  div.add-bargain input.el-input__inner{
    color: #333 !important;
    border-color: #BFCBE3;
  }

  div.add-bargain input.el-input__inner::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: #6c7378 !important;
  }
  div.add-bargain input.el-input__inner::-moz-placeholder { /* Firefox 19+ */
    color: #6c7378 !important;
  }
  div.add-bargain input.el-input__inner:-ms-input-placeholder { /* IE 10+ */
    color: #6c7378 !important;
  }
  div.add-bargain input.el-input__inner:-moz-placeholder { /* Firefox 18- */
    color: #6c7378 !important;
  }

</style>
<script>
  import { db } from '../../../../firebase/vueFire'
  import fireBase from '../../../../firebase/vueFire'
  import { Message } from 'element-ui'
  import _ from 'lodash'
  export default {
    data () {
      return {
        email: '',
        dialogVisible: true,
        title: '',
        selectedType: null,
        percent: null,
        listPrice: null,
        summary: null,
        productId: null,
        salePrice: null,
        top: "5%",
        selectedTag: [],
        mainUrl: null,
        origin: process.env.API_PATH,
        path: process.env.API_PATH + '/user/bargains',
        selectedStore: null,
        selectedBrand: null,
        selectedCategory: [],
        toolbarOptions: [
          ['bold', 'italic', 'underline', 'strike'],        // toggled buttons
          ['blockquote', 'code-block'],
          [{ 'header': 1 }, { 'header': 2 }],               // custom button values
          [{ 'list': 'ordered'}, { 'list': 'bullet' }],
          [{ 'script': 'sub'}, { 'script': 'super' }],      // superscript/subscript
          [{ 'indent': '-1'}, { 'indent': '+1' }],          // outdent/indent
          [{ 'direction': 'rtl' }],                         // text direction
          [{ 'size': ['small', false, 'large', 'huge'] }],  // custom dropdown
          [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
          [{ 'color': [] }, { 'background': [] }],          // dropdown with defaults from theme
          [{ 'font': [] }],
          [{ 'align': [] }],
          ['clean']                                         // remove formatting button
        ],
        formats: [
          'background',
          'bold',
          'color',
          'font',
          'code',
          'italic',
          'link',
          'size',
          'strike',
          'script',
          'underline',
          'blockquote',
          'header',
          'indent',
          'list',
          'align',
          'direction',
          'code-block',
          'formula'
        ],
        content: "",
        validOn: null,
        validUntil: null,
        storeOptions: [],
        brandOptions: [],
        tagOptions: [],
        fileList: [],
        categoryOptions: [],
        editorOption: {
          formats: this.formats,
          modules: {
            toolbar: this.toolbarOptions
          }
        },
        typeOptions: [{
          value: 'sale',
          label: 'Sale'
        }, {
          value: 'percentOff',
          label: 'Percent Off'
        }, {
          value: 'free',
          label: 'Free'
        }, {
          value: 'promotion',
          label: 'Promotion'
        }, {
          value: 'other',
          label: 'Other'
        }]
      }
    },
    computed: {
      isHttp () {
        if (!_.isEmpty(this.mainUrl)) {
          if (this.mainUrl.toLowerCase().includes('http://') || this.mainUrl.toLowerCase().includes('https://')) {
            return true
          } else {
            return false
          }
        } else {
          return false
        }
      }
    },
    methods: {
      onFileRemove (file) {
        if (!_.isEmpty(file)) {
          var index = _.findIndex(this.fileList, (num) => num.uid === file.uid)
          if (index !== -1) {
            this.fileList.splice(index, 1)
          }
        }
      },
      onClose () {
        this.file = {}
        this.$router.push({name: 'UserBargains'})
      },
      onFileSelected (file) {
        if (!_.isEmpty(file)) {
          if (file.type === "image/jpeg" || file.type === "image/png") {
            this.fileList.push(file)
          } else {
            Message({
              showClose: true,
              message: "Only JPG and PNG file type are accepted!",
              type: 'error'
            })
          }
        }
        return false
      },
      storeChange () {
        console.log(this.selectedStore)
      },
      validateBeforeSubmit () {
        if (this.selectedType !== null && this.selectedType !== undefined) {
          if (this.selectedType === 'sale') {
            this.$validator.attach('salePrice', 'required', {
              prettyName: 'Sale Price',
              context: () => this.salePrice,
              getter: (context) => context
            })
            this.$validator.attach('listPrice', 'required', {
              prettyName: 'List Price',
              context: () => this.listPrice,
              getter: (context) => context
            })
            this.$validator.detach('percent')
          } else if (this.selectedType === 'percentOff'){
            this.$validator.attach('percent', 'required', {
              prettyName: 'Percent',
              context: () => this.percent,
              getter: (context) => context
            })
            this.$validator.detach('salePrice')
            this.$validator.detach('listPrice')
          } else {
            this.$validator.detach('percent')
            this.$validator.detach('salePrice')
            this.$validator.detach('listPrice')
          }
        } else {
          this.$validator.detach('percent')
          this.$validator.detach('salePrice')
          this.$validator.detach('listPrice')
        }
        if (_.isEmpty(this.validOn)) {
          this.$validator.attach('validOn', 'required', {
            prettyName: 'valid on',
            context: () => this.validOn,
            getter: (context) => context
          })
        } else {
          this.$validator.detach('validOn')
        }

        if (_.isEmpty(this.validUntil)) {
          this.$validator.attach('validUntil', 'required', {
            prettyName: 'valid until',
            context: () => this.validUntil,
            getter: (context) => context
          })
        } else {
          this.$validator.detach('validUntil')
        }


        this.$validator.validateAll().then((result) => {
          var count = this.fileList.length
          var params = {
            title: this.title,
            details: this.content,
            mainUrl: this.mainUrl,
            categories: this.selectedCategory,
            type: this.selectedType,
            validOn: this.validOn,
            validUntil: this.validUntil,
            store: this.selectedStore,
            tags: this.selectedTag,
            summary: this.summary,
            images: []
          }

          if (!_.isEmpty(this.brand)) {
            params.brand = this.brand
          }

          if (!_.isEmpty(this.productId)) {
            params.productId = this.productId
          }

          if (this.selectedType.value === 'sale') {
            params.listPrice = parseFloat(this.listPrice) || 0
            params.salePrice = parseFloat(this.salePrice) || 0
          } else if (this.selectedType.value === 'percentOff') {
            params.percentOff = this.percent
          }

          if (count > 0) {
            this.fileList.map((file) => {
              var storageRef = db.ref('deals-photos/' + file.name)
              storageRef.put(file).then((image) => {
                storageRef.getDownloadURL().then((url) => {
                  var obj = {storageUri: file.name, url: url}
                  params.images.push(obj)
                  count--
                  if (count <= 0) {
                    this.createPost(this.path, params)
                  }
                }).catch((error) => {
                  console.log(error)
                  count--
                })
              }).catch((error) => {
                console.log(error)
                count--
                if (count <= 0) {
                  this.createPost(this.path, params)
                }
              })
            })
          } else {
            this.createPost(this.path, params)
          }
        }).catch((error) => {
          console.log(error)
        })
      },
      createPost (path, params) {
        this.$http.post(path, params).then((response) => {
          Message({
            showClose: true,
            message: 'Successfully Added The Bargain.',
            type: 'success'
          })
          this.$emit('onAddedSuccess')
          this.$router.push({name: 'UserBargains'})
        }, (error) => {
          Message({
            showClose: true,
            message: error.body.message,
            type: 'error'
          })
        })
      },
      resetPassword () {
        if (this.$validator.validate('email', this.email)) {
          var user = fireBase.auth()
          user.sendPasswordResetEmail(this.email).then(() =>{
            Message({
              showClose: true,
              message: 'Your password reset link has been sent to your email.',
              type: 'success'
            })
          }, function(error) {
            Message({
              showClose: true,
              message: error.message,
              type: 'error'
            })
          })
        } else {
          Message({
            showClose: true,
            message: 'There was an error when trying to reset your password.',
            type: 'error'
          })
        }
      }
    },
    mounted () {
      this.$ga.trackPage(this.$route.path)
      this.$http.get(this.origin + '/stores').then((response) => {
        this.storeOptions = response.body
        this.errors.clear()
      }).catch((error) => {
        this.options = []
      })
      this.$http.get(this.origin + '/brands').then((response) => {
        this.brandOptions = response.body
      }).catch((error) => {
        this.options = []
      })
      this.$http.get(this.origin + '/categories').then((response) => {
        this.categoryOptions = response.body
      }).catch((error) => {
        this.options = []
      })
      this.$http.get(this.origin + '/tags').then((response) => {
        this.tagOptions = response.body
      }).catch((error) => {
        this.options = []
      })
    }
  }
</script>



